@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;800&display=swap);
header {
  height: 80px;
  background-color: darkgoldenrod;
  padding: 0 10px;
}

nav {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-between;
}

.menu {
  display: flex;
}
a {
  font-size: 12px;
}

.nav__links {
  list-style: none;
  margin-left: 10px;
}

.logout__btn {
  border: none;
  background-color: transparent;
  font-size: 12px;
  cursor: pointer;
}

.container {
  height: 100vh;
}

main {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 10px;
}

.user__name {
  color: purple;
}

.login {
  height: 100vh;
  width: 100vw;
  display: flex;

  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login__form {
  display: flex;
  flex-direction: column;
}

.login__form > h1 {
  font-size: 35px;
  margin-bottom: 15px;
  padding-bottom: 10px;
  letter-spacing: -1px;
}

.login__form > input {
  min-width: 380px;
  padding: 20px 0 20px 15px;
  margin-bottom: 10px;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.24);
  border-radius: 2px;
  font-size: 15px;
}

.submit__btn {
  background-color: #111;
  color: white;
}

a {
  text-decoration: none;
  color: #111;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

