.login {
  height: 100vh;
  width: 100vw;
  display: flex;

  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login__form {
  display: flex;
  flex-direction: column;
}

.login__form > h1 {
  font-size: 35px;
  margin-bottom: 15px;
  padding-bottom: 10px;
  letter-spacing: -1px;
}

.login__form > input {
  min-width: 380px;
  padding: 20px 0 20px 15px;
  margin-bottom: 10px;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.24);
  border-radius: 2px;
  font-size: 15px;
}

.submit__btn {
  background-color: #111;
  color: white;
}

a {
  text-decoration: none;
  color: #111;
}
