header {
  height: 80px;
  background-color: darkgoldenrod;
  padding: 0 10px;
}

nav {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-between;
}

.menu {
  display: flex;
}
a {
  font-size: 12px;
}

.nav__links {
  list-style: none;
  margin-left: 10px;
}

.logout__btn {
  border: none;
  background-color: transparent;
  font-size: 12px;
  cursor: pointer;
}

.container {
  height: 100vh;
}

main {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
}

.user__name {
  color: purple;
}
